import type { ReactNode } from 'react';
import classNames from 'classnames';
import styles from './heading.module.scss';

export type HeadingProps = {
  /**
   * sets the heading children.
   */
  children?: ReactNode;

  /**
   * level of the heading.
   */
  level?: 1 | 2 | 3 | 4 | 5 | 6;

  /**
   * use a span element.
   */
  span?: boolean;
} & React.HTMLAttributes<HTMLHeadingElement>;

export function Heading({ children, span = false, level = 1, className, ...rest }: HeadingProps) {
  const Element: any = !span 
    ? `h${level}`
    : 'span';
  
  return (
    <Element {...rest} className={classNames(styles.heading, className)}>
      {children}
    </Element>
  );
}
